import { Component, HostListener, ViewChild } from '@angular/core';
import { AuthenticationService } from '../../service/auth.service';
import { Login, Message, Tags, Posts, Images, Category, TagModel, PostsEntity } from '../../model/index';
import { environment } from "../../../environments/environment";
import { Router, ActivatedRoute, NavigationEnd, ChildActivationStart, NavigationStart } from "@angular/router";
import { Title }     from '@angular/platform-browser';
import {OwlCarousel} from 'ngx-owl-carousel';

@Component({
  selector: 'app-home',
  templateUrl: 'home.component.html'
})
export class HomeComponent { 
  constructor(private authService: AuthenticationService,private router: Router, private titleService: Title) { }

  postNews: Posts[];
  postRecents: Posts[];
  postOfViews: Posts[];
  postOfRandom: Posts[];
  show =  13;
  totalPost = 0;
  API_URL: String = environment.API_URL;
  isLoadmore: boolean = true;
  isSpiner: boolean = false;
  allTags: Tags[] = [];
  categories: Category[] = [];
  showMostViews:any = 5;
  startMostViews: any = 0;
  totalPostOgViews:any = 0;
  isLoadMostViews: boolean = false;
  isLoadRandom: boolean = false;
  pageMostViews: any = 0;
  isNext: boolean = true;
  isPrev: boolean = true;
  isMobile: boolean = false;
  query: String;
  @ViewChild('owlElement') owlElement: OwlCarousel

  titleTT = 'Trang chủ gái xinh';

  options = {
    loop: true,
    margin: 10,
    nav: false,
    dots: true,

    autoplay:true,
    autoplayTimeout:5000,
    autoplayHoverPause:true,
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 1
      },
      740: {
        items: 2
      },
      940: {
        items: 2
      }
    }
  }

  ngOnInit(): void {
    this.options.dots = false;

    this.router.events
    .filter(event => event instanceof NavigationStart)
    .subscribe(event => this.titleService.setTitle(this.titleTT));

    this.router.events
    .filter(event => event instanceof NavigationEnd)
    .subscribe(event => this.titleService.setTitle(this.titleTT));

    this.postNews = [];

    //get posts news:
    this.authService.postNew().subscribe(res => {
      this.postNews = res.data as Posts[];
    })

    //get posts recents:
    this.authService.postNewRecent(this.show).subscribe(res => {
      this.postRecents = res.data as Posts[];
      this.totalPost = res.total;
      //console.log(this.totalPost);
    })

    //get all tags:
    this.authService.tagsList().subscribe(res => {
      this.allTags = res.data as Tags[];
    })

    //get total all categories:
    this.authService.categoriesTotal().subscribe(res => {
      this.categories = res.data as Category[];
    })

    //get posts most views:
    this.isLoadMostViews = true;

    this.authService.postOfViews(this.startMostViews, this.showMostViews).subscribe(res => {
      this.postOfViews = res.data as Posts[];
      this.pageMostViews = Math.ceil(res.total / this.showMostViews);
      this.isLoadMostViews = false;

      if(this.startMostViews === 0) {
        this.isPrev = false;
      }
    },err =>{
      this.isLoadMostViews = false;
    })

    //get post random:
    this.isLoadRandom = true;

    this.authService.postOfRandom(2).subscribe(res => {
      this.postOfRandom = res.data as Posts[];

      this.isLoadRandom = false;
    }, err => {
      this.isLoadRandom = false;
    })

    //get size window:
    let innerWidth: Number = Number(window.innerWidth);

    if(innerWidth < 990) {
      this.options.dots = false;
      this.isMobile = true;
      this.options.nav = true;
      this.options.autoplayTimeout = 3000;
    } else {
      this.options.dots = true
      this.options.nav = false;
      this.options.autoplayTimeout = 5000;
      this.isMobile = false;
    }
  }

  ngAfterViewInit() {
    this.titleService.setTitle(this.titleTT);
  }

  //load more post:
  loadMorePost() {
    this.show += 9;
    this.isSpiner = true;

    //try to get value:
    this.authService.postNewRecent(this.show).subscribe(res => {
      if(this.show >= this.totalPost) {
        this.isLoadmore = false;
      }

      this.postRecents = res.data as Posts[];
      this.isSpiner = false;

    },err => {
      this.isLoadmore = true;
      this.show -= 9;
      this.isSpiner = false;
    })
  }

  //next most views:
  nextMostViews(event: any): void {
    event.preventDefault();

    if(this.startMostViews < (this.pageMostViews - 1 )) {
      this.startMostViews +=1;

      this.isLoadMostViews = true;

      this.authService.postOfViews(this.startMostViews, this.showMostViews).subscribe(res => {
        this.postOfViews = res.data as Posts[];
        this.totalPostOgViews = res.total;
        this.isLoadMostViews = false;

        this.isPrev = true;
      },err =>{
        this.isLoadMostViews = false;
      })
    } 

    if(this.startMostViews === (this.pageMostViews - 1 )) {
      this.isNext = false;
    }
  }

  //prev most views:
  prevMostViews(event: any): void {
    event.preventDefault();

    if(this.startMostViews > 0) {
      this.startMostViews -=1;

      this.isLoadMostViews = true;

      this.authService.postOfViews(this.startMostViews, this.showMostViews).subscribe(res => {
        this.postOfViews = res.data as Posts[];
        this.totalPostOgViews = res.total;
        this.isLoadMostViews = false;

        this.isNext = true;
      },err =>{
        this.isLoadMostViews = false;
      })
    }
    
    if(this.startMostViews === 0) {
      this.isPrev = false;
    }
  }

  @HostListener('window:resize', ['$event'])
  onResize(event: any){
    if(event.target.innerWidth < 990) {
      this.owlElement.options.dots = false;
      this.owlElement.options.nav = true;
      this.owlElement.options.autoplayTimeout = 3000;
      this.isMobile = true;
    } else {
      this.owlElement.options.dots = true;
      this.owlElement.options.nav = false;
      this.owlElement.options.autoplayTimeout = 5000;
      this.isMobile = false;
    }

    this.owlElement.reInit();
  }

  //search on posts:
  searchQuery(): void {
    let keyWorld:String = this.query;

    if(keyWorld === 'undefined' || keyWorld === '' || keyWorld === null){
      return;
    }

    this.router.navigate(['/home/search/list'],{ queryParams: { query: keyWorld } });
  }
}
