import { Component, HostListener } from '@angular/core';
import { AuthenticationService } from '../../service/auth.service';
import { Login, Message, Tags, Posts, Images, Category, TagModel, PostsEntity } from '../../model/index';
import { environment } from "../../../environments/environment";
import { Router, ActivatedRoute, NavigationEnd, NavigationStart, Event } from "@angular/router";
import { NotificationService } from '../../service/notification.service';
import * as $ from 'jquery';
import { Title }     from '@angular/platform-browser';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'detail-app-home',
  templateUrl: 'detail.component.html'
})
export class HomeDetailComponent {
  
  constructor(private authService: AuthenticationService,private sanitizer: DomSanitizer, private titleService: Title, private notificationService :NotificationService, private activeRoute: ActivatedRoute, private router: Router) {
  }

  slug: any;
  post: Posts;
  pageId = '/post/';
  tags: Tags[];
  allTags: Tags[];
  images: Images[];
  API_URL: String = environment.API_URL;
  textCategory: String;
  views: Number = 0;
  idPost: Number = 0;
  slideIndex = 1;
  isMobile: boolean = false;
  categories: Category[] = [];
  ralateds: Posts[] = [];
  showRelated = 0;
  limitRelated = 4;
  couterPage = 1;
  pageRalated = 0;
  totalRelated = 0;
  isNext: boolean = true;
  isPrev: boolean = true;
  postOfRandom: Posts[] = [];
  isLoadPost: boolean = false;
  mediaValue: String;
  videoId: String;
  urLVideo: String;
  categoryId: number;
  query: String;

  ngOnInit(): void {
    this.post = new Posts();
    this.tags = [];
    this.allTags = [];
    this.images = [];
    this.videoId = '';
    this.urLVideo = '';

    this.slug = String(this.activeRoute.snapshot.paramMap.get('slug'));
    this.pageId = this.pageId + this.slug;

    //try to get post by slug:
    this.isLoadPost = true;
    this.authService.postBySlug(this.slug).subscribe(res => {
      this.post = res.data as Posts;
      this.tags = this.post.tags;
      this.images = this.post.images;
      this.textCategory = String(this.post.categories[0].name);
      this.categoryId = Number(this.post.categories[0].id);
      this.idPost = this.post.id;
      this.views = this.post.counter;

      this.isLoadPost = false;

      //set title:
      this.titleService.setTitle(String(this.post.title));

      //try to update view count:
      this.viewsCounter(this.post.id);

      //try to get all related:
      this.authService.postRealteds(this.post.id, this.post.tags).subscribe(res => {
        this.ralateds = res.data as Posts[];
        this.pageRalated =  Math.ceil(res.total / this.limitRelated);
        this.totalRelated = res.total;
      })

      //get media:
      let regExp = /^.*(youtu\.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
      let match = this.post.content.match(regExp);

      if (match && match[2].length == 11) {
        this.videoId =  match[2];
        this.urLVideo = 'https://www.youtube.com/embed/' + this.videoId;
      }

    }, err => {
      let status = String(err);

      if (status === "Error: 401") {
        this.notificationService.showWaring('Session time out', 'Notification');

        //set interval:
        setTimeout(() => {
          this.authService.sessionTimeOut();
        }, 2500);
      }
      else {
        this.router.navigate(['/404']);
      }
      this.isLoadPost = false;
    })

    //get all tags:
    this.authService.tagsList().subscribe(res => {
      this.allTags = res.data as Tags[];
    })

    //get size window:
    let innerWidth: Number = Number(window.innerWidth);

    if(innerWidth < 990) {
      this.isMobile = true;
    } else {
      this.isMobile = false;
    }

    //get total all categories:
    this.authService.categoriesTotal().subscribe(res => {
      this.categories = res.data as Category[];
    })

    if(this.couterPage ===1) {
      this.isPrev = false;
    }

    //load random list post:
    this.authService.postOfRandom(2).subscribe(res => {
      this.postOfRandom = res.data as Posts[];
    })
  }

  //event count views:
  viewsCounter(idPost: any): void {
    this.authService.updateViews(idPost).subscribe(res =>{
      let post: Posts = res.data as Posts;
      this.views = post.counter;
      //console.log(JSON.stringify(this.post.tags));
    })
  }

  //open modal images:
  openModal(): void {
    document.getElementById("myModal").style.display = "block";
  }

  //close modal:
  closeModal(): void {
    document.getElementById("myModal").style.display = "none";
  }

  //show current picture:
  currentSlide(number: any): void {
    console.log(number);
    this.showSlides(this.slideIndex = number);
  }

  //show slides:
  showSlides(n: any) {
    var i: any;
    var slides:any = document.getElementsByClassName("mySlides");
    var dots:any = document.getElementsByClassName("demo");
    var captionText: any = document.getElementById("caption");
    if (n > slides.length) {this.slideIndex = 1}
    if (n < 1) {this.slideIndex = slides.length}
    for (i = 0; i < slides.length; i++) {
        slides[i].style.display = "none";
    }
    for (i = 0; i < dots.length; i++) {
        dots[i].className = dots[i].className.replace(" active", "");
    }
    slides[this.slideIndex - 1].style.display = "block";
    dots[this.slideIndex - 1].className += " active";
    captionText.innerHTML = dots[this. slideIndex - 1].alt;
  }

  //next and prvous:
  plusSlides(number: any): void {
    this.showSlides(this.slideIndex += number);
  }

  @HostListener('window:resize', ['$event'])
  onResize(event: any){
    if(event.target.innerWidth < 990) {
      this.isMobile = true;
    } else {
      this.isMobile = false;
    }
  }

  goHref(envet: any, slug:any): void {
    event.preventDefault();
    this.router.navigate(['/home/' + slug]).then(success => {
      this.ngOnInit();
    });
  }

  nextMostViews(envet: any) {
    event.preventDefault();

    if(this.couterPage <= this.pageRalated) {
      if(this.couterPage === this.pageRalated){
        this.showRelated = 4 * (this.pageRalated -1);
        this.limitRelated = this.totalRelated;
      } 
      else {
        this.showRelated += 4;
        this.limitRelated +=4;
      }

      this.isPrev = true;
      
      this.couterPage +=1;
    }


    if(this.couterPage === this.pageRalated) {
      this.isNext = false;
    }
  }

  prevMostViews(event: any) {
    event.preventDefault();

    if(this.couterPage > 1) {
      if((this.couterPage -1) === this.pageRalated){
        this.showRelated = 4 * this.pageRalated - 4;
        this.limitRelated = 4 * this.pageRalated;
      } 
      else {
        this.showRelated -= 4;
        this.limitRelated -=4;
      }

      this.isNext = true;
    }

    this.couterPage -=1;

    if(this.couterPage === 1) {
      this.isPrev = false;
    }
  }

  goCategory(event:any) {
    event.preventDefault();

    this.router.navigate(['/home/category-tags/' + this.categoryId ], { queryParams: { query: 'category', name: this.textCategory } });
  }

  //search on posts:
  searchQuery(): void {
    let keyWorld:String = this.query;

    if(keyWorld === 'undefined' || keyWorld === '' || keyWorld === null){
      return;
    }

    this.router.navigate(['/home/search/list'],{ queryParams: { query: keyWorld } });
  }
}
