import { Component, HostListener } from '@angular/core';
import { TemplateRef, ViewChild } from '@angular/core';
import { AuthenticationService } from '../../service/auth.service';
import { Login, Message, Tags, Posts, Images, Category, TagModel, PostsEntity } from '../../model/index';
import { environment } from "../../../environments/environment";
import { Router, ActivatedRoute, NavigationStart, ChildActivationStart, NavigationEnd } from "@angular/router";
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { HomeDetailComponent } from '../../views/home/detail.component';

@Component({
  selector: 'layout-home',
  templateUrl: 'layout.component.html'
})
export class LayoutComponent { 
  categories: Category[] = [];
  showSearchBox: boolean = false;
  dataSearchs: Posts[] = [];
  showSniperSearch: boolean = false;
  API_URL: String = environment.API_URL;
  keySearch: String;
  modalRef: BsModalRef;
  isMobile: boolean = false;
  @ViewChild(HomeDetailComponent) homeDetail: HomeDetailComponent;

  constructor(private authService: AuthenticationService, private router: Router, private modalService: BsModalService) { }

  ngOnInit(): void {
    //get total all categories:
    this.authService.categoriesTotal().subscribe(res => {
      this.categories = res.data as Category[];
    })

    //get size window:
    let innerWidth: Number = Number(window.innerWidth);

    if(innerWidth < 990) {
      this.isMobile = true;
    } else {
      this.isMobile = false;
    }
  }

  //serach by key:
  onSearch(event:any): void {
    if(event.keyCode == 13){
      return;
    }

    let keyWorld:String = String(event.target.value)
    
    if(keyWorld === 'undefined' || keyWorld === '' || keyWorld === null) {
      this.showSearchBox = false;
      this.showSniperSearch = false;
    } else {
      this.keySearch = keyWorld;
      this.showSearchBox = true;
      this.showSniperSearch = true;
      
      //get list for search:
      this.authService.searchByName(keyWorld, 0).subscribe(res => {
        this.dataSearchs = res.data as Posts[];
        this.showSniperSearch = false;
        //console.log(JSON.stringify(this.dataSearchs));
      }, err => {
        this.showSniperSearch = false;
      })
    }
  }

  //detect click oustside
  @HostListener('window:click')
  private onClickBody(e:Event) {
    this.showSearchBox = false;
  }

  searchQuery(event: any) {
    let keyWorld:String = String(event.target.value);

    if(keyWorld === 'undefined' || keyWorld === '' || keyWorld === null){
      return false;
    }

    this.showSearchBox = false;
    this.showSniperSearch = false;

    this.router.navigate(['/home/search/list'],{ queryParams: { query: keyWorld } });
  }

  openModal(event: any, template: TemplateRef<any>) {
    event.preventDefault();
    this.modalRef = this.modalService.show(template);
  }

  //go to href:
  goHref(event: any, slug:any): void {
    event.preventDefault();

    this.router.navigateByUrl('/', {skipLocationChange: true}).then(()=>
        this.router.navigate(['/home/' + slug])
    );
  }

  @HostListener('window:resize', ['$event'])
  onResize(event: any){
    if(event.target.innerWidth < 990) {
      this.isMobile = true;
    } else {
      this.isMobile = false;
    }
  }
}
