    //create or update tags:
    public createOrUpdateTags(tags: Tags): Observable <any> {
        let myheaders = new Headers();
        myheaders.append('Content-Type','application/json');

        let message: Message = JSON.parse(localStorage.getItem('currentUser'));
        myheaders.append('Authorization','Bearer ' + message.access_token);
        
        let options = new RequestOptions({ headers:myheaders });
        let url = this.API_URL + '/api/admin/v1/createOrUpdateTags';
        
        return this.http.post(url, JSON.stringify(tags), options)
        .map(res => {
            return res.json();
        })
        .catch((error: any) => {
            if (error.status === 500) {
                return Observable.throw(new Error(error.status));
            }
            else {
                return Observable.throw(new Error(error.status));
            }
        });
    }

