import { Component, OnInit, HostListener } from '@angular/core';
import { AuthenticationService } from '../../service/auth.service';
import { Login, Message, Tags, Posts, Images, Category, TagModel, PostsEntity } from '../../model/index';
import { environment } from "../../../environments/environment";
import { Router, ActivatedRoute, NavigationStart, ChildActivationStart, NavigationEnd } from "@angular/router";
import { NotificationService } from '../../service/notification.service';
import { Title } from '@angular/platform-browser';

@Component({
    selector: 'search-home',
    templateUrl: 'search.component.html'
})
export class SearchComponent {
    constructor(private authService: AuthenticationService, private titleService: Title, private notificationService: NotificationService, private activeRoute: ActivatedRoute, private router: Router) {
    }

    id: any;
    query: any;
    nameQuery: any;
    name: any;
    isLoadMostViews: boolean = false;
    postOfViews: Posts[] = [];
    API_URL: String = environment.API_URL;
    showMostViews: any = 4;
    startMostViews: any = 0;
    totalPostOgViews: any = 0;
    pageMostViews: any = 0;
    isPrev: boolean = true;
    isNext: boolean = true;
    postOfRandom: Posts[] = [];
    postCatgoryTags: Posts[] = [];
    isLoadCategoryTags: boolean = false;
    totalPostTags = 0;
    start = 0;
    limit = 8;
    showLoadMore: boolean = true;
    searchList: Posts[] = [];
    totalResults: number = 25;
    pageAble = 0;
    isLoadSearchList: boolean = false;
    currentPage: number = 0;
    maxSize: number = 0;
    numPages: number = 0;
    queryText: String;
    results: boolean = true;
    title = 'Kết quả tìm kiếm';
    categories: Category[] = [];
    allTags: Tags[];
    isMobile: boolean = false;

    ngOnInit() {
        this.titleService.setTitle(this.title);
        this.query = this.activeRoute.snapshot.queryParams['query'];

        if (this.query === 'undefined' || this.query === '' || this.query === null) {
            this.router.navigate(['/404']);
        }

        //get result post of query:
        this.isLoadSearchList = true;

        this.authService.searchList(this.query, 1).subscribe(res => {
            this.searchList = res.data as Posts[];
            this.totalResults = res.total;
            this.pageAble = res.page;
            this.currentPage = res.currentPage;
            this.isLoadSearchList = false;
            this.maxSize = res.range;
            this.queryText = this.query;

            if (this.searchList.length === 0) {
                this.results = false;
            }
        }, err => {
            this.isLoadSearchList = false;
            this.results = false;
        })

        //get posts most views:
        this.isLoadMostViews = true;

        this.authService.postOfViews(this.startMostViews, this.showMostViews).subscribe(res => {
            this.postOfViews = res.data as Posts[];
            this.pageMostViews = Math.ceil(res.total / this.showMostViews);
            this.isLoadMostViews = false;

            if (this.startMostViews === 0) {
                this.isPrev = false;
            }
        }, err => {
            this.isLoadMostViews = false;
        })

        //load random list post:
        this.authService.postOfRandom(2).subscribe(res => {
            this.postOfRandom = res.data as Posts[];
        })

        //get total all categories:
        this.authService.categoriesTotal().subscribe(res => {
            this.categories = res.data as Category[];
        })

        //get all tags:
        this.authService.tagsList().subscribe(res => {
            this.allTags = res.data as Tags[];
        })

        //get size window:
        let innerWidth: Number = Number(window.innerWidth);

        if (innerWidth < 550) {
            this.isMobile = true;
        } else {
            this.isMobile = false;
        }
    }

    //prev most views:
    prevMostViews(event: any): void {
        event.preventDefault();

        if (this.startMostViews > 0) {
            this.startMostViews -= 1;

            this.isLoadMostViews = true;

            this.authService.postOfViews(this.startMostViews, this.showMostViews).subscribe(res => {
                this.postOfViews = res.data as Posts[];
                this.totalPostOgViews = res.total;
                this.isLoadMostViews = false;

                this.isNext = true;
            }, err => {
                this.isLoadMostViews = false;
            })
        }

        if (this.startMostViews === 0) {
            this.isPrev = false;
        }
    }

    //next most views:
    nextMostViews(event: any): void {
        event.preventDefault();

        if (this.startMostViews < (this.pageMostViews - 1)) {
            this.startMostViews += 1;

            this.isLoadMostViews = true;

            this.authService.postOfViews(this.startMostViews, this.showMostViews).subscribe(res => {
                this.postOfViews = res.data as Posts[];
                this.totalPostOgViews = res.total;
                this.isLoadMostViews = false;

                this.isPrev = true;
            }, err => {
                this.isLoadMostViews = false;
            })
        }

        if (this.startMostViews === (this.pageMostViews - 1)) {
            this.isNext = false;
        }
    }

    loadMorePost(): void {
        if (this.limit < this.totalPostTags) {
            this.limit += 8;
        }

        if (this.limit >= this.totalPostTags) {
            this.showLoadMore = false;
        }
    }

    goHref(event: any, id: any, queryTag: any, nameTag: any): void {
        event.preventDefault();
        this.router.navigate(['home/category-tags/' + id], { queryParams: { query: queryTag, name: nameTag } }).then(success => {
            this.ngOnInit();
        });
    }

    setPage(pageNo: number): void {
        this.currentPage = pageNo;
    }

    pageChanged(event: any): void {
        this.currentPage = event.page;
        //get result post of query:
        this.isLoadSearchList = true;

        //try to get pageing:
        this.authService.searchList(this.query, this.currentPage).subscribe(res => {
            this.searchList = res.data;
            this.currentPage = res.currentPage;
            this.isLoadSearchList = false;

            this.results = false;
        }, err => {
            this.isLoadSearchList = false;
            this.results = false;
        })
    }

    //search query:
    searchQuery() {
        this.isLoadSearchList = true;

        this.searchList = [];

        this.router.navigate(['/home/search/list'], { queryParams: { query: this.query } });

        this.authService.searchList(this.query, 1).subscribe(res => {
            this.searchList = res.data as Posts[];
            this.totalResults = res.total;
            this.pageAble = res.page;
            this.currentPage = res.currentPage;
            this.isLoadSearchList = false;
            this.maxSize = res.range;
            this.queryText = this.query;

            if (this.searchList.length === 0) {
                this.results = false;
            }
        }, err => {
            this.isLoadSearchList = false;
            this.results = false;
        })
    }

    @HostListener('window:resize', ['$event'])
    onResize(event: any) {
        if (event.target.innerWidth < 550) {
            this.isMobile = true;
        } else {
            this.isMobile = false;
        }
    }
}
