import { BrowserModule, Title } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { LocationStrategy, HashLocationStrategy, PathLocationStrategy } from '@angular/common';

import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { PERFECT_SCROLLBAR_CONFIG } from 'ngx-perfect-scrollbar';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  suppressScrollX: true
};

import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';

// Import containers
import { DefaultLayoutComponent } from './containers';

import { P404Component } from './views/error/404.component';
import { P500Component } from './views/error/500.component';
import { LoginComponent } from './views/login/login.component';
import { HomeComponent } from './views/home/home.component';
import { HomeDetailComponent } from './views/home/detail.component';
import { CategoryTagsComponent } from './views/home/category-tags.component';
import { RegisterComponent } from './views/register/register.component';
import { LayoutComponent } from './views/layout/layout.component';
import { SearchComponent } from './views/home/search.component';

import { AuthenticationService } from './service/auth.service';
import { AdminService } from './service/admin.service';
import { NotificationService } from './service/notification.service';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations'; 
import { SafePipe } from './helpers/safe.pipe';

const APP_CONTAINERS = [
  DefaultLayoutComponent
];

import {
  AppAsideModule,
  AppBreadcrumbModule,
  AppHeaderModule,
  AppFooterModule,
  AppSidebarModule,
} from '@coreui/angular';

// Import routing module
import { AppRoutingModule } from './app.routing';
import { ToastrModule } from 'ngx-toastr';

// Import 3rd party components
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { TabsModule } from 'ngx-bootstrap/tabs';
import { ChartsModule } from 'ng2-charts/ng2-charts';
import { from } from 'rxjs';

import { NgxLoadingModule } from 'ngx-loading';
import { ErrorInterceptor } from './helpers/error.interceptor';
import { DataTablesModule } from 'angular-datatables';
import { DisqusModule } from 'ngx-disqus';

// Tooltip Component
import { TooltipModule } from 'ngx-bootstrap/tooltip';
import { ContentLoaderModule } from '@netbasal/content-loader';
// Pagination Component
import { PaginationModule } from 'ngx-bootstrap/pagination';

// Import your library
import { OwlModule } from 'ngx-owl-carousel';
// RECOMMENDED
import { ModalModule } from 'ngx-bootstrap/modal';

@NgModule({
  imports: [
    BrowserModule,
    AppRoutingModule,
    AppAsideModule,
    AppBreadcrumbModule.forRoot(),
    AppFooterModule,
    AppHeaderModule,
    AppSidebarModule,
    PerfectScrollbarModule,
    BsDropdownModule.forRoot(),
    TabsModule.forRoot(),
    ChartsModule,
    HttpModule,
    HttpClientModule,
    FormsModule,
    ToastrModule.forRoot(),
    BrowserAnimationsModule,
    NgxLoadingModule.forRoot({}),
    DataTablesModule,
    OwlModule,
    DisqusModule.forRoot('my-first-blog-6'),
    TooltipModule.forRoot(),
    ContentLoaderModule,
    PaginationModule.forRoot(),
    ModalModule.forRoot()
  ],
  declarations: [
    AppComponent,
    ...APP_CONTAINERS,
    P404Component,
    P500Component,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    LayoutComponent,
    HomeDetailComponent,
    CategoryTagsComponent,
    SearchComponent,
    SafePipe
  ],
  providers: [
    {
      provide: LocationStrategy,
      useClass: PathLocationStrategy
    },
    AuthenticationService,
    NotificationService,
    AdminService,
    { 
      provide: HTTP_INTERCEPTORS, 
      useClass: ErrorInterceptor,
      multi: true 
    },
    Title
  ],
  bootstrap: [ AppComponent ]
})
export class AppModule { }
