import { Component } from '@angular/core';
import { AuthenticationService } from '../../service/auth.service';
import { from } from 'rxjs';
import { Login, Message } from '../../model/index';
import { Router } from "@angular/router";
import { NotificationService } from '../../service/notification.service';
import { Title }     from '@angular/platform-browser';


@Component({
  selector: 'app-dashboard',
  templateUrl: 'login.component.html'
})
export class LoginComponent { 
  login: Login;
  public loading = false;
  title ='Login to Admin';

  public constructor(private authService: AuthenticationService, private titleService: Title, private router: Router, private notificationService: NotificationService){};

  ngOnInit() {
      this.login = new Login();
  }

  public loGin(){
    this.loading = true;
    this.titleService.setTitle(this.title);

    this.authService.login(this.login).subscribe( res => {
       //  console.log('=====> RES:'+JSON.stringify(res));
       this.router.navigate(['/admin']);
       this.loading = false;
    },
    err => {

       let status = String(err);
       if(status === "Error: 401") {
           this.notificationService.showWaring('Username or password not correct', 'Notification');
       }

       this.loading = false;
    }
   );
  }
}
